    .logo {
        filter: brightness(120%);
        left: 0;
        position: absolute;
        .logoLink {
            display: inline-block;
            padding: 0;
            margin: 0 0 0 10vw;
            top: 2.5px;
            position: relative;
            img {
                float: left;
